import { useReducer, createContext, useState, useEffect } from 'react';
import './App.scss';
// Components
import { Table } from './c/table';
import TableClass from './c/tableClass';
import { ControlBar_1 } from './c/controlbar_1';
import { FAQ } from './c/faq';
import { UpDownBtns } from './c/up_down_btns';

function reducer(state, action) {
  const [type, pay] = [action.type, action.payload]
  const pos = pay ? pay.position : null
  const table = pos ? state.company[pos.p].table : null

  switch (type) {
    case 'copyFromClipboard':
      const { part, col } = { ...action.payload };
      let data = action.payload.data.split('\r\n');
      for (let i = 0; i < data.length; i++) {
        state.company[part].table.setCol(col, data)
        if (part == 0 && col == 1 && state.company[part].table.getCell(+col + 1, i).data === "") { // Если название компании в строке пустое, копируем из групп
          state.company[part].table.setCell(+col + 1, i, data[i])
        }
      }
      return { ...state }

    case 'toggleRowDisable':
      const { p, x, y } = { ...action.payload.position }
      if (state.company[p] !== undefined) {
        state.company[p].table.toggleRowDisable(y, action.payload.value);
      }
      return { ...state }

    case 'updateOption':
      window.o[pay.key] = pay.value
      state[pay.key] = pay.value

      if (pay.key === "quotable") {
        pay.value === "Нет" ? state.company[0].table.unquotable() : state.company[0].table.quotable(pay.value)
      }

      return { ...state }

    case "selectingCell":
      state.company[pay.position.p].table.setSelectedCell(pay.value ? pay.position : null)
      return { ...state }

    case "copyModOff":
      state.company.forEach((t) => {
        t.table.setSelectedCell(null)
      })
      return { ...state }

    case "setCell":
      table.setCell(pos.x, pos.y, pay.value)
      return { ...state }

    case "clearCol":
      state.company[pay.part].table.clearCol(pay.col)
      return { ...state }

    case "clearAll":
      state.company[pay].table.clearAll()
      return { ...state }

    case "sortColumn":
      state.company[pay.part_index].table.sortColumn(pay.col_index, pay.sort_title)
      return { ...state }

    case "cutSquareBrackets":
      state.company[pay].table.cut_square_brackets()
      return { ...state }

    case "replace":
      state.company[pay.part_index].table.replace(pay.from, pay.to)
      return { ...state }

    case "changeColFromReplaceBar":
      state.company[pay.part_index].table.changeColFromReplaceBar(pay.col_index, pay.value)
      return { ...state }

    case "updateFrequency":
      state.company[pay.part_ind].table.updateFrequency(pay.row_ind, pay.value)
      return { ...state }

    case "addRow":
      state.company[pay].table.addRow()
      return { ...state }

    default:
      throw new Error();
  }
}

export let AppContext = createContext(null);

function App() {

  const [state, dispatch] = useReducer(reducer, {
    // OPTIONS
    walletCount: 3,
    method: "STAG",
    quotable: "Нет",
    // PARTs
    company: [
      {
        table: new TableClass(5, 0).init()
      },
      {
        table: new TableClass(5, 1).init()
      }
    ],
  })

  const [init, setInit] = useState(false)

  useEffect(() => {
    document.body.addEventListener('mouseup', (e) => {
      dispatch({
        type: "copyModOff"
      })
    })
    document.body.addEventListener("click", () => {
      const openMenus = document.querySelectorAll(".options.open")
      openMenus.forEach((om) => {
        om.classList.remove('open')
      })
    })
    setInit(true)
    window.o = { method: "STAG" }
  }, [init])

  return (
    <AppContext.Provider value={{ state, dispatch }}>
      <div className="part">
        <Table part="0" />
        <ControlBar_1 part="0" />
      </div>
      <FAQ />
      <UpDownBtns />
    </AppContext.Provider>
  );
}

export default App;