import { useState } from "react"
import { useEffect } from "react";
import { useContext } from "react"
import { AppContext } from "../App"

export function Cell(props) {
  const { state, dispatch } = useContext(AppContext)
  const d = props.data.options.disabled
  const [value, setValue] = useState("")
  const [selected, setSelected] = useState(false)
  const [spellcheckerError, spellcheckerErrorArray] = useState([])

  const change = (e) => {
    const val = e.target.value;
    setValue(val)
    dispatch({
      type: "setCell",
      payload: {
        position: props.position,
        value: val
      }
    })
  }

  const selecting = (e, val) => {
    dispatch({
      type: "selectingCell",
      payload: {
        node: e.target.closest('.cell-wrapper'),
        position: props.position,
        value: val
      }
    })
  }

  const copy = () => {
    const SC = state.company[props.position.p].table.selectedCell
    if (SC) {
      const SCvalue = state.company[props.position.p].table.getCell(SC.x, SC.y)
      if (SC.x === props.position.x) { // только в своей колонке
        dispatch({
          type: "setCell",
          payload: {
            position: props.position,
            value: SCvalue.data
          }
        })
      }
    }
  }

  const spellcheck = (e) => {
    if (props.spellcheck) {
      const word = e.target.value
      fetch('https://speller.yandex.net/services/spellservice.json/checkText?text=' + word)
        .then(res => res.json())
        .then(res => {
          spellcheckerErrorArray(res)
        })
    }
  }

  const getFrequency = (n) => {
    const MPval = state.method.split(" ")[1]
    const MP = MPval !== undefined ? MPval : 8

    if (n >= 5000) return "СВЧ"
    else if (n >= 1000) return "ВЧ"
    else if (n >= 100) return "СЧ"
    else if (n >= MP) return "НЧ"
    else return "-"
  }

  useEffect(() => {
    // Если изменяюетя YW пересчитываем частотность
    if (+props.position.p === 0 && +props.position.x === 3) {
      dispatch({
        type: "updateFrequency",
        payload: {
          part_ind: props.position.p,
          row_ind: props.position.y,
          value: getFrequency(+value)
        }
      })
    }
  }, [value])

  return (
    <CellOptions {...props} d={d}>
      <label
        onMouseDown={(e) => selecting(e, true)}
        onMouseUp={(e) => selecting(e, false)}
        onMouseMove={copy}>

        <SpellcheckerErrorIndicator d={spellcheckerError} />

        <input
          type={props.type}
          onBlur={spellcheck}
          className="cell-input"
          value={props.data.data}
          onChange={change}
          disabled={d} />
        {selected && <div className="selected-marker" onClick={(e) => null}></div>}
      </label>
    </CellOptions>
  )
}

function SpellcheckerErrorIndicator(props) {
  if (props.d.length === 0) return null;
  else {
    return (
      <div className="spellchecker-error-indicator">
        <i class="bi bi-question-octagon"></i>
      </div>
    )
  }
}

function CellOptions(props) {

  const beforeOptions = () => {
    if (props.part === "0" && props.col === "0") {
      return <ActionOption {...props} icon="bi bi-eye" />
    }
  }

  const afterOptions = () => {
    return null;
  }

  return (
    <div className="cell-wrapper" disabled={props.d}>
      <div className="before">{beforeOptions()}</div>
      {props.children}
      <div className="after">{afterOptions()}</div>
    </div>
  )
}

function ActionOption(props) {

  const { state, dispatch } = useContext(AppContext);

  const action = () => {
    dispatch({
      type: 'toggleRowDisable',
      payload: { position: props.position, value: props.d }
    })
  }

  return (
    <>
      <span className="row-number">{props.index + 1}</span>
      <i onClick={action} className={props.icon}></i>
    </>
  )
}